import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./jc-check-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class JcCheckAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
//	@ViewChild(ToastComponent) toast: ToastComponent;

 
jcCheckForm: FormGroup;
checkId = 0;  
ajaxData : JcCheckData[];
jcCheck= new JcCheckData();
status;
items=[];
models=[];

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.checkId).subscribe(params=>{
			if(params.checkId != undefined){
				this.checkId=params.checkId; 
			}
		}); 
	console.log("checkId=="+this.checkId);
	if(this.checkId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
		this.populateDropDownData();
	}
	}
	
    ngOnInit() {
		this.jcCheckForm = new FormGroup({			
			'checkName' : new FormControl('', [ Validators.required]),
			'checkType' : new FormControl('', [ Validators.required]),
			'checkItemId' : new FormControl('', [ Validators.required]),
			'checkEntryId' : new FormControl(''),
			'checkEntryDate' : new FormControl(''),
			'checkModifiedId' : new FormControl(''),
			'checkModifiedDate' : new FormControl(''),
		});
	}
	  
	get checkItemId(){ return this.jcCheckForm.get('checkItemId')};
	get checkName(){ return this.jcCheckForm.get('checkName')};
	get checkType(){ return this.jcCheckForm.get('checkType')};
	get checkEntryId(){ return this.jcCheckForm.get('checkEntryId')};
	get checkEntryDate(){ return this.jcCheckForm.get('checkEntryDate')};
	get checkModifiedId(){ return this.jcCheckForm.get('checkModifiedId')};
	get checkModifiedDate(){ return this.jcCheckForm.get('checkModifiedDate')}; 
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<JcCheckData>(sessionStorage.getItem('requestUrl')+'/axelacrm/service/jcchecklist-data ',
							 			JSON.stringify({ 'checkId' :this.checkId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.jcCheck = data['populateData'][0];
																		console.log("checkModifiedId=="+this.jcCheck.checkModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
	 
	  populateDropDownData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  	this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
							 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.items = data['populateBranch']
			  															});
		  
		  	this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
							 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.models = data['populateBranch']
			  															});
	  }
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/customer-group-list']);      //path should be changed
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.jcCheck[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.jcCheckForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.checkId == 0){ 
				this.jcCheck['requestType'] = 'add';
			}else{
				this.jcCheck['requestType'] = 'update';
				this.jcCheck['checkId'] = this.checkId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/jcchecklist',
									JSON.stringify(this.jcCheck),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
	//			  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.jcCheckForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.jcCheck);
	  }
 
  onDelete(){
		 this.jcCheck['requestType'] = 'delete';
		this.jcCheck['checkId'] = this.checkId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/jcCheck',
									JSON.stringify(this.jcCheck),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
//			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.jcCheckForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

	
export class  JcCheckData {
    checkId:string;
	checkItemId:string;
	checkName:string;
	checkType:string;
	checkEntryId: string;
	checkEntryDate: string;
	checkModifiedId: string;
	checkModifiedDate: string;
  
}


